import React, { useState } from 'react';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer, PieChart, Pie, Cell, Line, LineChart } from 'recharts';

const ImprovedVisualizations = ({ playerStatsArray, winningsTrendData }) => {
  const [selectedPlayer, setSelectedPlayer] = useState(null);
  const COLORS = ['#8884d8', '#82ca9d', '#ffc658', '#ff7300', '#0088FE', '#00C49F', '#FFBB28', '#FF8042'];

  const averageReturnData = playerStatsArray.map(player => ({
    name: player.player,
    return: parseFloat(player.return)
  }));

  const gamesPlayedData = playerStatsArray.map(player => ({
    name: player.player,
    gamesPlayed: player.gamesPlayed
  }));

  const playerTrendData = winningsTrendData.map(game => ({
    date: game.date,
    [selectedPlayer]: game[selectedPlayer]
  }));

  const CustomTooltip = ({ active, payload, label }) => {
    if (active && payload && payload.length) {
      return (
        <div className="bg-white p-2 border rounded shadow">
          <p className="font-bold">{`${label}`}</p>
          <p>{`${payload[0].name}: ${payload[0].value.toFixed(2)}${payload[0].unit || ''}`}</p>
        </div>
      );
    }
    return null;
  };

  return (
    <div className="space-y-8 p-4">
      <div>
        <h2 className="text-lg font-semibold mb-2">Average Return by Player</h2>
        <ResponsiveContainer width="100%" height={200}>
          <BarChart data={averageReturnData} margin={{ top: 5, right: 5, bottom: 5, left: 5 }}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="name" tick={{ fontSize: 10 }} interval={0} angle={-45} textAnchor="end" height={50} />
            <YAxis unit="%" tick={{ fontSize: 10 }} />
            <Tooltip content={<CustomTooltip />} />
            <Bar dataKey="return" fill="#8884d8">
              {averageReturnData.map((entry, index) => (
                <Cell key={`cell-${index}`} fill={entry.return > 0 ? '#82ca9d' : '#ff7300'} />
              ))}
            </Bar>
          </BarChart>
        </ResponsiveContainer>
      </div>

      <div>
        <h2 className="text-lg font-semibold mb-2">Games Played</h2>
        <ResponsiveContainer width="100%" height={200}>
          <PieChart>
            <Pie
              data={gamesPlayedData}
              dataKey="gamesPlayed"
              nameKey="name"
              cx="50%"
              cy="50%"
              outerRadius={80}
              label={({ name, percent }) => `${name} ${(percent * 100).toFixed(0)}%`}
              labelLine={false}
            >
              {gamesPlayedData.map((entry, index) => (
                <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
              ))}
            </Pie>
            <Tooltip content={<CustomTooltip />} />
          </PieChart>
        </ResponsiveContainer>
      </div>

      <div>
        <h2 className="text-lg font-semibold mb-2">Player Winnings Trend</h2>
        <select 
          className="mb-2 p-2 border rounded"
          value={selectedPlayer || ''}
          onChange={(e) => setSelectedPlayer(e.target.value)}
        >
          <option value="">Select a player</option>
          {playerStatsArray.map(player => (
            <option key={player.player} value={player.player}>{player.player}</option>
          ))}
        </select>
        {selectedPlayer && (
          <ResponsiveContainer width="100%" height={200}>
            <LineChart data={playerTrendData} margin={{ top: 5, right: 5, bottom: 5, left: 5 }}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="date" tick={{ fontSize: 10 }} interval={1} angle={-45} textAnchor="end" height={50} />
              <YAxis tick={{ fontSize: 10 }} />
              <Tooltip content={<CustomTooltip />} />
              <Line type="monotone" dataKey={selectedPlayer} stroke="#8884d8" dot={false} />
            </LineChart>
          </ResponsiveContainer>
        )}
      </div>
    </div>
  );
};

export default ImprovedVisualizations;